import React from 'react';
import Button from '../Button/Button';

import PrimaryTypoGraphy from '../TypoGraphy/TypoGraphy';

import './Modal.css';

const Modal = ({ id, title, children, show, onClose, options }) => {
  if (!show) return null;
  return (
    <div className="overlay">
      <div className="modal" id={id}>
        <div>
          <div id="titleModal">
            <div className="titleOfmodal">
              <PrimaryTypoGraphy>
                <h2>{title}</h2>
              </PrimaryTypoGraphy>
            </div>
            <div>
              <PrimaryTypoGraphy>
                <button
                  className="close"
                  onClick={() => {
                    onClose();
                  }}
                >
                  X
                </button>
              </PrimaryTypoGraphy>
            </div>
          </div>
          <div className="SplitterTitle" />
          <div className="content">{children}</div>
          <div className="actions">
            <PrimaryTypoGraphy>
              {(options && options.button) || (
                <Button onClick={onClose}>Ok</Button>
              )}
            </PrimaryTypoGraphy>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Modal;
