import { useEffect } from 'react';
import { AirPlaneIcon, BagsIcon, SFIcon } from '../../components/Icon/Icon';
import Modal from '../../components/Modal/Modal';

const GiftTable = ({ onClose, show }) => {
  useEffect(() => {
    const el = document.getElementById('giftTableModal');
    if (el)
      el.scrollIntoView({
        behavior: 'smooth',
      });
  }, []);
  return (
    <Modal
      id="giftTableModal"
      legend="El regalo es opcional"
      onClose={onClose}
      show={show}
      title="Mesa de Regalos"
    >
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-evenly',
        }}
      >
        <SFIcon />
        <BagsIcon />
        <AirPlaneIcon />
      </div>
      <p>
        En nuestra maleta no cabe todo lo que nos quieres regalar para nuestro
        nuevo hogar.{' '}
      </p>
      <p>
        Sin embargo, si quieres tener un detalle con nosotros, puedes
        depositarlo en un buzón especial el día de la boda.
      </p>
    </Modal>
  );
};
export default GiftTable;
