import React, { useState, useEffect } from 'react';
import './OurMarriage.css';
import TypoGraphy from '../../components/TypoGraphy/TypoGraphy';
import conf from '../../config/config';
import { useSpring, animated, useTransition, config } from 'react-spring';
import GiftTable from '../GiftTable/GifTable';

import ConfirmAssitence from '../Confirm/Confirm';

import images from '../ImgCore/ImgProccess';

const Splitter = () => <div className="Splitter" />;
const MarrigeElement = ({ val1, val2 }) => (
  <div className="MarrigeElement">
    <div>
      <TypoGraphy>
        <h1>{val1}</h1>
      </TypoGraphy>
    </div>
    <div>
      <TypoGraphy>
        <h3>&mdash; {val2} &mdash;</h3>
      </TypoGraphy>
    </div>
  </div>
);

const buildArray = () => {
  const list = Object.entries(images).map((image, index) => {
    return {
      id: index,
      img: image[1],
    };
  });
  return list;
};

const slides = buildArray();

const OurMarriage = () => {
  const [index, set] = useState(0);
  const [isConfirming, setisConfirming] = useState(false);
  let interval;
  const transitions = useTransition(slides[index], (item) => item.id, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
    config: config.molasses,
  });

  useEffect(() => {
    if (!isConfirming) {
      interval = setInterval(
        () => set((state) => (state + 1) % slides.length),
        15000
      );
    }

    return () => clearInterval(interval);
  }, [isConfirming]);

  const [showGiftModal, setShowGiftModal] = useState(false);
  const [showConfirm, setShowConfirm] = useState(false);
  const slideProps = useSpring({ width: '100%', from: { width: '0%' } });
  const slideProps2 = useSpring({ width: '100%', from: { width: '0%' } });

  return (
    <>
      {transitions.map(
        ({ item, key, props }) =>
          item && (
            <animated.div
              key={key}
              className="Bg"
              style={{
                ...props,
                backgroundImage: `url(${item.img})`,
                backgroundPosition: 'center center',
              }}
            >
              <div className="Page">
                <GiftTable
                  show={showGiftModal}
                  onClose={() => {
                    setShowGiftModal(false);
                  }}
                />
                <ConfirmAssitence
                  show={showConfirm}
                  onClose={() => {
                    setShowConfirm(false);
                    setisConfirming(false);
                  }}
                />

                <animated.div className="WhiteBox">
                  <div style={{ paddingTop: '20px', paddingBottom: '20px' }}>
                    <TypoGraphy>
                      <p style={{ fontWeight: 'bold' }}>
                        <span className="hearth">&hearts;</span>¡¡¡NOS
                        CASAMOS!!! <span className="hearth">&hearts;</span>
                      </p>
                    </TypoGraphy>
                  </div>
                  <animated.div style={slideProps} className="Row">
                    <div></div>
                    <MarrigeElement
                      val1={`${conf.Bride.name}`}
                      val2={conf.Bride.lastName}
                    />
                    <div className="MarrigeElement">
                      <h3 style={{ fontSize: '2em' }}>&#38;</h3>
                    </div>
                    <MarrigeElement
                      val1={conf.Groom.name}
                      val2={conf.Groom.lastName}
                    />
                  </animated.div>
                  <div>
                    <Splitter></Splitter>
                    <animated.div style={slideProps2}>
                      <TypoGraphy>
                        <p className="Text">
                          QUEREMOS INVITARLO A NUESTRO MATRIMONIO CIVIL QUE SE
                          REALIZARA EL DIA
                        </p>
                      </TypoGraphy>
                    </animated.div>
                    <Splitter></Splitter>
                    <div className="Address">
                      <div
                        className="Grid1"
                        style={{ display: 'flex', flexDirection: 'column' }}
                      >
                        <TypoGraphy>
                          <span>{conf.MarriageDate.dayName}</span>
                        </TypoGraphy>
                        <span className="spanMarriage">
                          {conf.MarriageDate.day}
                          {` `}
                          {conf.MarriageDate.month}
                        </span>
                        <TypoGraphy>
                          <span>{conf.MarriageDate.year}</span>
                        </TypoGraphy>
                      </div>

                      <div className="Grid2">
                        <TypoGraphy>
                          <a
                            rel="noreferrer"
                            className="Link"
                            href="https://www.google.com.mx/maps/dir//Real+Inn+Canc%C3%BAn,+Boulevard+Kukulcan,+Kukulcan+Boulevard,+Zona+Hotelera,+Canc%C3%BAn,+Quintana+Roo/@21.1430376,-86.8480916,12z/data=!3m1!4b1!4m8!4m7!1m0!1m5!1m1!1s0x8f4c28d972cf6cdb:0x6f0ddb3e6612f6e5!2m2!1d-86.778051!2d21.143052"
                            target="_blank"
                          >
                            <span>{conf.Location.street}</span>
                          </a>
                          <span>{conf.Location.neighborhood}</span>
                          <span>{conf.Location.city}</span>
                          <span>{conf.Location.country}</span>
                        </TypoGraphy>
                      </div>
                      <div className="Grid3">
                        <span className="spanMarriage">5 PM </span>
                        <TypoGraphy>
                          <span>Vestimenta </span>
                          <span>M:Casual Formal H:Guayabera</span>
                        </TypoGraphy>
                      </div>
                    </div>
                  </div>
                  <Splitter />
                  <div style={{ padding: '10px' }}>
                    <div className="RowActions">
                      <div>
                        <TypoGraphy>
                          <a
                            onClick={(e) => {
                              e.preventDefault();
                              setShowGiftModal(true);
                            }}
                            className="Link"
                            href="/"
                            rel="noreferrer"
                          >
                            {' '}
                            Mesa de Regalo
                          </a>
                        </TypoGraphy>
                      </div>
                      <div>
                        <TypoGraphy>
                          <a
                            className="Link"
                            href="/"
                            onClick={(e) => {
                              e.preventDefault();
                              setShowConfirm(true);
                              clearInterval(interval);
                              setisConfirming(true);
                            }}
                            rel="noreferrer"
                          >
                            Confirmar asistencia
                          </a>
                        </TypoGraphy>
                      </div>
                      <div>
                        <TypoGraphy>
                          <a
                            rel="noreferrer"
                            className="Link"
                            target="_blank"
                            href="https://www.google.com.mx/maps/dir//Real+Inn+Canc%C3%BAn,+Boulevard+Kukulcan,+Kukulcan+Boulevard,+Zona+Hotelera,+Canc%C3%BAn,+Quintana+Roo/@21.1430376,-86.8480916,12z/data=!3m1!4b1!4m8!4m7!1m0!1m5!1m1!1s0x8f4c28d972cf6cdb:0x6f0ddb3e6612f6e5!2m2!1d-86.778051!2d21.143052"
                          >
                            Ver Ubicacion del Hotel
                          </a>
                        </TypoGraphy>
                      </div>
                    </div>
                  </div>
                </animated.div>
              </div>
            </animated.div>
          )
      )}
    </>
  );
};

export default OurMarriage;
