import React, { useState, useEffect } from 'react';
import emailjs from 'emailjs-com';
import Button from '../../components/Button/Button';
import Modal from '../../components/Modal/Modal';
import './Confirm.css';
import PrimaryTypoGraphy from '../../components/TypoGraphy/TypoGraphy';

function isValidEmail(email) {
  const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}
function isNumber(evt) {
  evt = evt ? evt : window.event;
  var charCode = evt.which ? evt.which : evt.keyCode;
  if (charCode > 31 && (charCode < 48 || charCode > 57)) {
    return false;
  }
  return true;
}

const Form = ({ onChangeHandler, fields }) => (
  <div className="Form">
    <div>
      <input
        email
        placeholder="Correo"
        type="email"
        name="user_email"
        value={fields['user_email']}
        onChange={onChangeHandler}
      />
    </div>
    <div>
      <input
        type="text"
        placeholder="Nombre"
        name="user_name"
        value={fields['user_name']}
        onChange={onChangeHandler}
        required
      />
    </div>
    <div>
      <input
        onKeyPress={isNumber}
        min={1}
        max={5}
        type="number"
        placeholder="Acompañantes"
        name="guess_number"
        value={fields['guess_number']}
        onChange={onChangeHandler}
        required
      />
    </div>
  </div>
);

const ConfirmAssitence = ({ onClose, show }) => {
  const [fields, setFields] = useState({});
  const [success, setSuccess] = useState(false);
  const [fail, setFail] = useState(false);

  const onChangeHandler = (event) => {
    event.preventDefault();
    const name = event.target.name;
    const value = event.target.value;
    setFields((prev) => ({
      ...prev,
      [name]: value,
    }));
  };
  const options = {
    button: (
      <Button
        disabled={
          !fields['user_email'] ||
          !fields['user_name'] ||
          !fields['guess_number'] ||
          !isValidEmail(fields['user_email'])
        }
        onClick={() => sendConfirm()}
      >
        Confirmar
      </Button>
    ),
  };

  const sendConfirm = () => {
    emailjs
      .send(
        'service_9ovqxbq',
        'template_uinwfch',
        {
          ...fields,
        },
        'user_FlBqdlqahyPphU2z5M92T'
      )
      .then(
        (result) => {
          console.log(result.text);
          setSuccess(true);
        },
        (error) => {
          console.log(error.text);
          setFail(true);
        }
      );
    return false;
  };

  useEffect(() => {
    const el = document.getElementById('confirmModal');
    if (el)
      el.scrollIntoView({
        behavior: 'smooth',
      });
  });

  return (
    <Modal
      id="confirmModal"
      options={options}
      onClose={onClose}
      show={show}
      title="Asistencia"
    >
      {!success && !fail && (
        <Form onChangeHandler={onChangeHandler} fields={fields} />
      )}
      {success && (
        <div className="success">
          <PrimaryTypoGraphy>
            <p>
              Gracias por confirmar tu asistencia , te esperamos en el Hotel
              Real Inn a las 5:00 PM{' '}
            </p>
          </PrimaryTypoGraphy>
        </div>
      )}
      {fail && (
        <div className="error">
          <PrimaryTypoGraphy>
            <p>Algo salio mal :( , intentalo de nuevo</p>
          </PrimaryTypoGraphy>
        </div>
      )}
    </Modal>
  );
};

export default ConfirmAssitence;
